import mapaa from '../../components/mapView.vue'
export default {
	components: {
		mapaa
	},
	data() {
		return {
			// 类型选择
			btnList: [
				{
					type: '分区查看',
					color: 'bg-gradual-green',
					iconType: 'cuIcon-attention'
				},
				{
					type: '分类查看',
					// color: 'bg-gradual-blue',
					color: 'bg-gradual-green',
					iconType: 'cuIcon-mail'
				},
				{
					type: '在线地图',
					// color: 'bg-gradual-purple',
					color: 'bg-gradual-green',
					iconType: 'cuIcon-mail'
				}
			],
			changeView: 0,
			list: [],
			tabCur: 0,
			mainCur: 0,
			verticalNavTop: 0,
			load: true,
			msglist: [
				{
					name: '空气温湿度传感器',
					type: '大气监测',
					isOpen: true
				},
				{
					name: '土壤PH值传感器',
					type: '土壤监测',
					isOpen: true
				},
				{
					name: '叶面温湿度传感器',
					type: '植物监测',
					isOpen: false
				},
				{
					name: '交流自动虫情测报灯',
					type: '虫情监测',
					isOpen: false
				},
				{
					name: '气象站',
					type: '小型监测系统',
					isOpen: true
				},
				{
					name: '网关',
					type: '通讯设备',
					isOpen: false
				}
			]
		};
	},
	onLoad() {
		uni.showLoading({
			title: '加载中...',
			mask: true
		});
		let list = [{}];
		for (let i = 0; i < 26; i++) {
			list[i] = {};
			list[i].name = String.fromCharCode(65 + i);
			list[i].id = i;
		}
		this.list = list;
		this.listCur = list[0];
	},
	onReady() {
		uni.hideLoading()
	},
	methods: {
		/* 修改设备开关状态 */
		SetShadow(e,index) {
			let msg = e.isOpen?'关闭':'打开'
			uni.showModal({
			    title: '警告',
			    content: '确认'+ msg +'此设备',
			    success: res => {
			        if (res.confirm) {
			            console.log('用户点击确定');
						console.log(e)
						e.isOpen = !e.isOpen
						console.log(this.msglist)
						if(e.isOpen === true) {
							uni.showToast({
							    title: '已打开设备',
							    duration: 2000
							});
						}else {
							uni.showToast({
							    title: '已关闭设备',
							    duration: 2000
							});
						}
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
			
			
		},
		/* 切换类型页面 */
		changeViewFun(e) {
			this.changeView = e
		},
		TabSelect(e) {
			this.tabCur = e.currentTarget.dataset.id;
			this.mainCur = e.currentTarget.dataset.id;
			this.verticalNavTop = (e.currentTarget.dataset.id - 1) * 50
		},
		VerticalMain(e) {



			let that = this;
			let tabHeight = 0;
			console.log(tabHeight)
			if (this.load) {
				for (let i = 0; i < this.list.length; i++) {
					let view = uni.createSelectorQuery().select("#main-" + this.list[i].id);
					view.fields({
						size: true
					}, data => {
						this.list[i].top = tabHeight;
						tabHeight = tabHeight + data.height;
						this.list[i].bottom = tabHeight;
					}).exec();
				}
				this.load = false
			}
			let scrollTop = e.detail.scrollTop + 10;
			for (let i = 0; i < this.list.length; i++) {
				if (scrollTop > this.list[i].top && scrollTop < this.list[i].bottom) {
					this.verticalNavTop = (this.list[i].id - 1) * 50
					this.tabCur = this.list[i].id
					console.log(scrollTop)
					return false
				}
			}
		}
	},
}
